export default class Station {
    constructor(title, efficiencyLevel, efficiencyRelativeAmount){
        this.title = title;
        this.efficiencyLevel = efficiencyLevel;
        this.efficiencyRelativeAmount = efficiencyRelativeAmount;
        this.incoming = [];
        this.outcoming = [];
    }
}



